import Swiper, { Navigation, Pagination } from 'swiper';
Swiper.use([Navigation, Pagination]);


let mql = window.matchMedia('(min-width: 1024px)');
let certSlider = false;

function certificateSliderInit() {
  let certificateSlider = new Swiper('.certificates__slider', {
    slidesPerView: 3,
    spaceBetween: 130,
    navigation: {
      nextEl: ".swiper-button-next",
      prevEl: ".swiper-button-prev",
    },
    breakpoints: {
      992: {
        slidesPerView: 2,
        spaceBetween: 90,
      },
      1200: {
        slidesPerView: 3,
        spaceBetween: 130,
      }
    },
    init: true
  });

  certSlider = true;
}

if (isDesktop()) {
  certificateSliderInit();
}

window.addEventListener('resize', () => {
  if (mql.matches) {
    let certificateSlider = new Swiper('.certificates__slider', {
      slidesPerView: 3,
      spaceBetween: 130,
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },
      breakpoints: {
        992: {
          slidesPerView: 2,
          spaceBetween: 90,
        },
        1200: {
          slidesPerView: 3,
          spaceBetween: 130,
        }
      },
      init: true
    });

    certSlider = true;
  } else {
    if (certSlider) {
      certificateSlider.destroy();
    }
  }
})

const objectsSlider = new Swiper('.objects__slider', {
  slidesPerView: 1,
  spaceBetween: 130,
  navigation: {
    nextEl: ".swiper-button-next",
    prevEl: ".swiper-button-prev",
  },
});

const aboutSlider = new Swiper('.about__slider', {
  slidesPerView: 2,
  spaceBetween: 25,
  navigation: {
    nextEl: ".swiper-button-next",
    prevEl: ".swiper-button-prev",
  },
  breakpoints: {
    768: {
      slidesPerView: 2,
      spaceBetween: 90,
    },
    1200: {
      slidesPerView: 3,
      spaceBetween: 130,
    }
  },
});

